/****************Navbar.jsx*******************/
nav {
  display: block;
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.336);
  position: fixed;
  left: 0;
  top: 30px;
  z-index: 1;
  color: #fff;
  width: 70px;
  height: 140px;
}

.NavBar_logo__3EshW img {
  border-radius: 20px;
  width: 90px;
  padding: 5px;
  cursor: pointer;
}

/* .icon10:hover ul{
  display: block;
} */
.NavBar_icon10__1Yi5q{
  color: #fff;
  font-size: 40px;
  margin-top: 20px;
  width: 50px;
  height: 30px;
}

nav:hover ul {
  display: block;
}
nav ul {
  display: none;
  background: rgba(255, 255, 255, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  width: 250px;
  height: 76vh;
  padding-right: 50px;
  list-style: none;
}
ul li {
  box-sizing: border-box;
  width: 13em;
  height: 3em;
  font-size: 20px;
  border-radius: 00.5em;
  color: #fff;
  font-family: sans-serif;
  text-transform: capitalize;
  line-height: 3em;
  transition: 0.3em;
  cursor: pointer;
}
ul li:nth-child(odd) {
  /* background-color: linear-gradient(to right, #4941e0, #dc4f4f); */
  text-align: left;
  padding-left: 10%;
  -webkit-transform: perspective(500px) rotateY(45deg);
          transform: perspective(500px) rotateY(45deg);
}
ul li:nth-child(even) {
  /* background-color: linear-gradient(to left, #4941e0, #dc4f4f); */
  text-align: right;
  padding-left: 10%;
  -webkit-transform: perspective(500px) rotateY(-45deg);
          transform: perspective(500px) rotateY(-45deg);
}
ul li:nth-child(odd):hover {
  padding-left: 5%;
  -webkit-transform: perspective(200px) rotateY(45deg);
          transform: perspective(200px) rotateY(45deg);
}
ul li:nth-child(even):hover {
  padding-right: 5%;
  -webkit-transform: perspective(200px) rotateY(-45deg);
          transform: perspective(200px) rotateY(-45deg);
}

.NavBar_active__3s4EI {
  background-color: crimson;
  color: #000000;
  box-shadow: 5px 10px 30px rgba(198, 73, 64, 0.411);
  transition: all ease 0.2s;
}
ul .NavBar_h2__3-YQl {
  color: #fff;
  font-size: 100px;
  color: #FFFFFF;
  margin-top: 8px;
  text-shadow: 12px 2px 0 crimson, 12px -2px 0 crimson, -2px 2px 0 crimson, -2px -2px 0 crimson, 12px 0px 0 crimson, 0px 2px 0 crimson, -2px 0px 0 crimson, 0px -2px 0 crimson;
  -webkit-text-decoration: underline crimson;
          text-decoration: underline crimson;
  width: 120px;
  height: 120px;
}
ul .NavBar_h2para__2weN_{
  color: #fff;
  font-size: 15px;
  font-weight: bold;
  margin: -80px 0 23px 20px;
}
ul img {
  border-radius: 20px;
  width: 100%;
  height: 110px;
  padding: 0.5em;
}
a {
  text-decoration: none;
}
nav ul li a {
  font-size: 1.3rem;
  font-family: calibri;
  height: 40px;
  line-height: 43px;
  margin: 3px;
  padding: 0px 22px;
  display: flex;
  text-transform: uppercase;
  font-weight: 500;
  color: #ffffff;
  letter-spacing: 1px;
  border-radius: 3px;
  transition: 0.2s ease-in-out;
}
nav ul li a:hover {
  background-color: crimson;
  color: #000000;
  box-shadow: 5px 10px 30px rgba(198, 73, 64, 0.411);
  transition: all ease 0.2s;
  cursor: pointer;
}
nav .NavBar_menuBtn__GfX-8,
.NavBar_menuIcon__32itl {
  display: none;
}

button {
  outline: none;
  border: none;
}

input {
  outline: none;
  border: none;
}

label {
  color: #fff;
}

