/* Animation properties */
@-webkit-keyframes home_spin-letters__18Kw9 {

    0%,
    10% {
        opacity: 0;
        -webkit-transform: rotateY(90deg);
                transform: rotateY(90deg);
    }

    30% {
        opacity: 1;
    }

    70%,
    86% {
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
        opacity: 1;
    }

    95%,
    100% {
        opacity: 0;
    }
}
@keyframes home_spin-letters__18Kw9 {

    0%,
    10% {
        opacity: 0;
        -webkit-transform: rotateY(90deg);
                transform: rotateY(90deg);
    }

    30% {
        opacity: 1;
    }

    70%,
    86% {
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
        opacity: 1;
    }

    95%,
    100% {
        opacity: 0;
    }
}

@-webkit-keyframes home_move-byline__3xvpW {
    0% {
        -webkit-transform: translateZ(5em);
                transform: translateZ(5em);
    }

    100% {
        -webkit-transform: translateZ(0);
                transform: translateZ(0);
    }
}

